.Content {
  width: 100%;
}
.Err, .HideErr {
  transition: 300ms;
  text-align: center;
  color: black;
  font-size: 15px;
  margin: 10px 0;
  font-weight: 500;
}
.HideErr {
  opacity: 0;
  visibility: hidden;
  margin: 0;
  z-index: -1;
}
.StockText {
  color: rgb(var(--dark-blue2));
  text-align: center;
  margin: 10px auto 15px;
  padding: 0 10px;
  max-width: 400px;
}
.Article {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  animation: fadeIn 350ms ease-in forwards;
}
.ArticleInfo {
  width: calc(100% - 185px);
}
.Article img {
  width: 175px;
  height: 175px;
  object-fit: contain;
  margin-left: 10px;
}
.Article a {
  color: black;
  text-decoration: none;
  font-weight: bold;
  transition: 300ms;
}
.Article a:hover {
  color: rgb(var(--blue));
}
.Summary {
  margin: 10px 0;
}
.Date {
  margin-top: 10px;
  color: rgb(112, 112, 112);
}
.Sentiment {
  margin: 0 0 10px;
  animation: fadeIn 350ms ease-in forwards;
}
.Sentiment div {
  text-align: center;
}
.Sentiment span {
  font-weight: 500;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
.GeneralBtn {
  margin-right: 10px;
}
.GeneralBtn button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px 5px 3px 10px;
  margin: auto;
  border-radius: 0;
}
.GeneralBtn svg {
  margin-left: 2px;
  font-size: 25px;
}
.Inputs {
  margin: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 50px;
}
.ArticleInfo3 {
  display: none;
}
@media (max-width: 490px) {
  .Inputs {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .GeneralBtn {
    margin: 25px 0 15px;
  }
  .Err {
    margin: 35px 0 5px;
  }
  .Sentiment {
    margin: 10px 0;
  }
}
@media (max-width: 480px) {
  .Article {
    flex-wrap: wrap;
    position: relative;
  }
  .ArticleInfo {
    width: 100%;
  }
  .ArticleInfo2 {
    display: none;
  }
  .ArticleInfo3 {
    display: block;
    width: calc(100% - 185px);
  }
}
